.portrait-wrapper {
  display: contents;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 40px;
  width: 220px;
//   max-width: calc(100% - 20px - 20px);
  text-decoration: none;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(20px + 10%);
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .icon {
  left: -2px;
  top: -2px;
}

.portrait[data-style="tiny"] .icon {
  display: none;
}

.portrait-image {
  width: 100%;
  margin-bottom: 20px;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
  margin: 0;
}

.portrait[data-style="large"] .portrait-image {
}

.portrait-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  white-space: nowrap; /* 防止换行 */
}

.portrait-description {
  white-space: nowrap; /* 防止描述换行 */
  overflow: hidden;
  text-overflow: ellipsis; /* 超长显示省略号 */
}

.portrait[data-style="tiny"] {
  .portrait-description,
  .portrait-affiliation {
    display: none;
  }
}